import React, { Component } from 'react';
import Trust from '../Parts/Trust';




class ForexZone extends Component {
  render() {
    return (
      <div id="ThePage">
        <div className="title-container d-flex justify-content-center align-items-center">
          <div className="container ">
            <h1 className="text-white">Forex Zone</h1>
          </div>
        </div>
        <div className="container">
            <div className="row py-5 justify-content-between">
            <div className="col-md pl-0 pr-5">
            <div className="entry">
                <p>Trading in foreign currency can provide an excellent income. People enter forex trading, for their own reasons, but mainly because of the freedom it can provide. You can trade only one hour a day and still make a very nice income. Regardless of the reason, we believe that trading in foreign currency is the best place for you to begin trading. The opportunities in this market, and the profit you can make every day, are huge. The forex market operates 24 hours a day, so that if you work full-time you can still execute transactions, and make money. Of course the foreign currency market is speculative and you can lose significant amounts of money, which is why it is not suitable for everyone. Success depends on self-discipline, knowledge and correct timing. To do so we are here to offer you the suitable tools and a dedicated course suited to your needs. </p>
                <p>Among the many reasons, there is one significant reason why forex is the best place to trade, the fact that it is full of beginning traders. These traders are inexperienced and do not have the appropriate knowledge, which leads them to trade incorrectly and irresponsibly. We know that you pay for your mistakes in addition to feeling frustrated. Unfortunately (or luckily) we need such people in the market in order to profit from this situation. There are two types of groups: the group of skilled traders, and the group of experimenting traders who are not skilled enough and want to get rich fast. The question is  to which group do you want to belong? </p>
                <p>The significant advantage of foreign exchange trade is that anyone can do it. Regardless of your background, anyone can open an account in one of the various trading arenas and begin to place trades. If you want to trade before going to work, or when you come home, or maybe even once a week on weekends, you can adjust your trading style to your lifestyle and still make additional income in order to pay the bills. The minute you learn how to do it correctly, these skills will be at your disposal for the rest of your life. The markets will always be here and you will be able to participate in them, even when you retire. You can decide whether you want to be part of this, and take a basic foreign currency trading course that will provide you with the skills you need be a skilled trader.</p>
                <p>What is the basic foreign currency course and how can it help me?</p>
                <p>A basic course is vital for your trading and is the backbone of your success. You never build a building without strong foundations, everyone needs foundations before they begin a new skill, and trading is no different. Beginning on the right foot is essential for your success and self-confidence. Foreign currency transactions and consistent profits do not just happen. You need to educate yourself, to have an excellent understanding of the foreign currency market, as well as self-discipline. The course is intended for beginners and includes everything you need in order to begin to trade in the foreign currency market today.</p>
                <p>Among the topics covered in detail in the course are: currencies, indices, trade arenas and graphs. Starting out correctly is vital, and you can be sure that people who neglect the foundations will struggle down the road. Educate yourself and the journey will be easier. The course provides in-depth explanations on how to define and read graphs correctly so that you can navigate easily. It also offers guidance regarding which brokers to work with and which to avoid. </p>
                <p>In the course you will learn how to execute transactions in the different trading arenas and using various software programs. This is an area in which most people get lost and need help.</p>
                <p>We are here to assist and support every new foreign currency trader, to help you avoid the common obstacles encountered by 90% of traders! All you need is an open mind and the passion to succeed!</p>
                <p>Leave your details today.</p>
            </div>
            </div>
                <div className="col-md-4">
                   <Trust/>
                </div>
            </div>
        </div>

      </div>
    );
  }
}

export default ForexZone;



